@media (max-width: 768px) {
  .container__projects .container__desc {
    margin: 50px 5px 15px;
  }
}

.container__desc {
  color: var(--color-blue);
}

@media (max-width: 768px) {
  .container__desc {
    margin: 50px 5px;
  }
}

.container__content .projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 -10px 0px 10px;
}

.container__content .projects__card {
  display: grid;
  grid-template-columns: 1fr 100px;
  align-items: center;
  width: 47%;
  margin: 0 10px 15px 0;
}

@media (max-width: 768px) {
  .container__content .projects__card {
    width: 100%;
  }
}

.container__content .projects__card-title {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 2;
  color: var(--color-blue);
  text-transform: uppercase;
}

.container__content .projects__card-title h2 {
  font-size: 22px;
  font-weight: 600;
}

.container__content .projects__card-desc {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 3;
  font-size: 18px;
  padding-right: 10px;
}

@media (max-width: 768px) {
  .container__content .projects__card-desc {
    font-size: 16px;
  }
}

.container__content .projects__card-img {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 4;
  width: 90px;
  height: 90px;
  justify-self: end;
}

.container__content .projects-btn {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 4;
  margin: 10px 0 20px 0;
  justify-self: left;
}

@media (max-width: 768px) {
  .container__content .projects-btn {
    justify-self: center;
  }
}

.container__content .projects-btn .btn-link {
  height: 40px;
  line-height: 40px;
  width: 150px;
  border-radius: 20px;
}
